import React, { useEffect } from "react"
import apartment from "../images/property/apartment.jpg"
import plots from "../images/property/plot.jpg"
import villa from "../images/property/villa.jpg"
import township from "../images/property/township.jpg"
import mall from "../images/property/mall.jpg"
import commercialComplex from "../images/property/commercial-complex.jpg"
import holidayHome from "../images/property/holiday-homes.jpg"
import farmHouse from "../images/property/farm-houses.jpg"
import { useState } from "react"

const HomePropertyType = () => {
  const [activeMainTab, setActiveMainTab] = useState(null)
  const [activeSubTab, setActiveSubTab] = useState(null)
  const [subTabsVisible, setSubTabsVisible] = useState({})

  const handleMainTabClick = tabId => {
    setActiveMainTab(tabId)
    // Automatically select the first sub-tab
    const firstSubTab = document.querySelector(`#${tabId} .sub_tabs .sub-tab`)
    if (firstSubTab) {
      setActiveSubTab(firstSubTab.getAttribute("data-target").substring(1))
    }
    console.log(firstSubTab)
  }

  const handleSubTabClick = subTabId => {
    setActiveSubTab(subTabId)
  }

  const toggleSubTabsList = listId => {
    setSubTabsVisible(prevState => ({
      ...prevState,
      [listId]: !prevState[listId],
    }))
  }

  const hideSubTabsList = () => {
    setSubTabsVisible({})
  }

  useEffect(() => {
    document.addEventListener("click", hideSubTabsList)
    return () => {
      document.removeEventListener("click", hideSubTabsList)
    }
  }, [])

  useEffect(() => {
    // Set default active tab on load
    const defaultActiveMainTab =
      document
        .querySelector(".main_tabs .tab.active")
        ?.getAttribute("data-target")
        .substring(1) ||
      document
        .querySelector(".main_tabs .tab")
        ?.getAttribute("data-target")
        .substring(1)
    if (defaultActiveMainTab) {
      setActiveMainTab(defaultActiveMainTab)
      const firstSubTab = document.querySelector(
        `#${defaultActiveMainTab} .sub_tabs .sub-tab`
      )
      if (firstSubTab) {
        setActiveSubTab(firstSubTab.getAttribute("data-target").substring(1))
      }
    }
  }, [])

  return (
    <div className="home_type_block">
      <div className="container">
        <div className="home_type_header">
          <h2 className="animate__animated animate__fadeInUp">Types</h2>
          <div className="main_tabs">
            <button
              type="button"
              aria-label="Top Properties"
              className={`tab ${
                activeMainTab === "top-properties" ? "active" : ""
              } animate__animated animate__fadeInUp`}
              data-target="#top-properties"
              onClick={() => handleMainTabClick("top-properties")}
              onKeyDown={e => {
                if (e.key === "Enter" || e.key === " ") {
                  handleMainTabClick("top-properties")
                }
              }}
            >
              Top Properties
            </button>
            <button
              type="button"
              aria-label="Top Properties"
              className={`tab ${
                activeMainTab === "residential-properties" ? "active" : ""
              } animate__animated animate__fadeInUp`}
              data-target="#residential-properties"
              onClick={() => handleMainTabClick("residential-properties")}
              onKeyDown={e => {
                if (e.key === "Enter" || e.key === " ") {
                  handleMainTabClick("residential-properties")
                }
              }}
            >
              Residential Properties
            </button>
          </div>
        </div>
        <div className="home_type_content">
          <div
            className="home_type_area"
            id="top-properties"
            style={{
              display: activeMainTab === "top-properties" ? "flex" : "none",
            }}
          >
            <div className="home_type_category">
              <button
                type="button"
                aria-label="Apartments"
                className="sub_tab_drop animate__animated animate__fadeInUp"
                onClick={e => {
                  e.stopPropagation()
                  toggleSubTabsList("sub_tabs-top")
                }}
                onKeyDown={e => {
                  if (e.key === "Enter" || e.key === " ") {
                    e.stopPropagation()
                    toggleSubTabsList("sub_tabs-top")
                  }
                }}
              >
                Apartments
              </button>
              <div
                className="sub_tabs"
                style={{
                  display: subTabsVisible["sub_tabs-top"] ? "block" : "none",
                }}
              >
                <button
                  type="button"
                  aria-label=""
                  className={`sub-tab ${
                    activeSubTab === "apartments" ? "active" : ""
                  }`}
                  data-target="#apartments"
                  onClick={() => handleSubTabClick("apartments")}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' || e.key === ' ') {
                      handleSubTabClick("apartments");
                    }
                  }}
                >
                  Apartments
                </button>
                <button
                  type="button"
                  aria-label=""
                  className={`sub-tab ${
                    activeSubTab === "plots" ? "active" : ""
                  }`}
                  data-target="#plots"
                  onClick={() => handleSubTabClick("plots")}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' || e.key === ' ') {
                      handleSubTabClick("plots");
                    }
                  }}
                >
                  Plots
                </button>
                <button
                  type="button"
                  aria-label=""
                  className={`sub-tab ${
                    activeSubTab === "villas" ? "active" : ""
                  }`}
                  data-target="#villas"
                  onClick={() => handleSubTabClick("villas")}
                  
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' || e.key === ' ') {
                      handleSubTabClick("villas");
                    }
                  }}
                >
                  Villas
                </button>
                <button
                  type="button"
                  aria-label=""
                  className={`sub-tab ${
                    activeSubTab === "townships" ? "active" : ""
                  }`}
                  data-target="#townships"
                  onClick={() => handleSubTabClick("townships")}
                  
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' || e.key === ' ') {
                      handleSubTabClick("townships");
                    }
                  }}
                >
                  Townships
                </button>
                <button
                  type="button"
                  aria-label=""
                  className={`sub-tab ${
                    activeSubTab === "malls" ? "active" : ""
                  }`}
                  data-target="#malls"
                  onClick={() => handleSubTabClick("malls")}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' || e.key === ' ') {
                      handleSubTabClick("malls");
                    }
                  }}
                >
                  Malls
                </button>
                <button
                  type="button"
                  aria-label=""
                  className={`sub-tab ${
                    activeSubTab === "commercial-complex" ? "active" : ""
                  }`}
                  data-target="#commercial-complex"
                  onClick={() => handleSubTabClick("commercial-complex")}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' || e.key === ' ') {
                      handleSubTabClick("commercial-complex");
                    }
                  }}
                >
                  Commercial Complex
                </button>
                <button
                  type="button"
                  aria-label=""
                  className={`sub-tab ${
                    activeSubTab === "holiday-homes" ? "active" : ""
                  }`}
                  data-target="#holiday-homes"
                  onClick={() => handleSubTabClick("holiday-homes")}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' || e.key === ' ') {
                      handleSubTabClick("holiday-homes");
                    }
                  }}
                >
                  Holiday Homes
                </button>
                <button
                  type="button"
                  aria-label=""
                  className={`sub-tab ${
                    activeSubTab === "farm-houses" ? "active" : ""
                  }`}
                  data-target="#farm-houses"
                  onClick={() => handleSubTabClick("farm-houses")}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' || e.key === ' ') {
                      handleSubTabClick("farm-houses");
                    }
                  }}
                >
                  Farm Houses
                </button>
              </div>
            </div>
            <div className="home_type_content">
              <div
                id="apartments"
                style={{
                  display: activeSubTab === "apartments" ? "flex" : "none",
                }}
              >
                <img src={apartment} alt="apartment" width={780} height={455} />
                <h3 className="animate__animated animate__bounce animate__delay-05s">
                  Apartments
                </h3>
              </div>
              <div
                id="plots"
                style={{ display: activeSubTab === "plots" ? "flex" : "none" }}
              >
                <img src={plots} alt="apartment" width={780} height={455} />
                <h3 className="animate__animated animate__bounce animate__delay-05s">
                  plots
                </h3>
              </div>
              <div
                id="villas"
                style={{ display: activeSubTab === "villas" ? "flex" : "none" }}
              >
                <img src={villa} alt="apartment" width={780} height={455} />
                <h3 className="animate__animated animate__bounce animate__delay-05s">
                  villas
                </h3>
              </div>
              <div
                id="townships"
                style={{
                  display: activeSubTab === "townships" ? "flex" : "none",
                }}
              >
                <img src={township} alt="apartment" width={780} height={455} />
                <h3 className="animate__animated animate__bounce animate__delay-05s">
                  townships
                </h3>
              </div>
              <div
                id="malls"
                style={{ display: activeSubTab === "malls" ? "flex" : "none" }}
              >
                <img src={mall} alt="apartment" width={780} height={455} />
                <h3 className="animate__animated animate__bounce animate__delay-05s">
                  malls
                </h3>
              </div>
              <div
                id="commercial-complex"
                style={{
                  display:
                    activeSubTab === "commercial-complex" ? "flex" : "none",
                }}
              >
                <img
                  src={commercialComplex}
                  alt="apartment"
                  width={780}
                  height={455}
                />
                <h3 className="animate__animated animate__bounce animate__delay-05s">
                  commercial complex
                </h3>
              </div>
              <div
                id="holiday-homes"
                style={{
                  display: activeSubTab === "holiday-homes" ? "flex" : "none",
                }}
              >
                <img
                  src={holidayHome}
                  alt="apartment"
                  width={780}
                  height={455}
                />
                <h3 className="animate__animated animate__bounce animate__delay-05s">
                  holiday homes
                </h3>
              </div>
              <div
                id="farm-houses"
                style={{
                  display: activeSubTab === "farm-houses" ? "flex" : "none",
                }}
              >
                <img src={farmHouse} alt="apartment" width={780} height={455} />
                <h3 className="animate__animated animate__bounce animate__delay-05s">
                  farm houses
                </h3>
              </div>
            </div>
          </div>
          <div
            className="home_type_area"
            id="residential-properties"
            style={{
              display:
                activeMainTab === "residential-properties" ? "flex" : "none",
            }}
          >
            <div className="home_type_category">
              <button
                type="button"
                aria-label="Apartments"
                className="sub_tab_drop animate__animated animate__fadeInUp"
                onClick={e => {
                  e.stopPropagation()
                  toggleSubTabsList("sub_tabs-top2")
                }}
                onKeyDown={e => {
                  if (e.key === "Enter" || e.key === " ") {
                    e.stopPropagation()
                    toggleSubTabsList("sub_tabs-top2")
                  }
                }}
              >
                Apartments
              </button>
              <div
                className="sub_tabs"
                style={{
                  display: subTabsVisible["sub_tabs-top2"] ? "block" : "none",
                }}
              >
                <button
                  type="button"
                  aria-label=""
                  className={`sub-tab ${
                    activeSubTab === "residential-apartments" ? "active" : ""
                  }`}
                  data-target="#residential-apartments"
                  onClick={() => handleSubTabClick("residential-apartments")}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' || e.key === ' ') {
                      handleSubTabClick("residential-apartments");
                    }
                  }}
                >
                  Apartments
                </button>
                <button
                  type="button"
                  aria-label=""
                  className={`sub-tab ${
                    activeSubTab === "residential-plots" ? "active" : ""
                  }`}
                  data-target="#residential-plots"
                  onClick={() => handleSubTabClick("residential-plots")}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' || e.key === ' ') {
                      handleSubTabClick("residential-plots");
                    }
                  }}
                >
                  Plots
                </button>
                <button
                  type="button"
                  aria-label=""
                  className={`sub-tab ${
                    activeSubTab === "residential-villas" ? "active" : ""
                  }`}
                  data-target="#residential-villas"
                  onClick={() => handleSubTabClick("residential-villas")}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' || e.key === ' ') {
                      handleSubTabClick("residential-villas");
                    }
                  }}
                >
                  Villas
                </button>
                <button
                  type="button"
                  aria-label=""
                  className={`sub-tab ${
                    activeSubTab === "residential-townships" ? "active" : ""
                  }`}
                  data-target="#residential-townships"
                  onClick={() => handleSubTabClick("residential-townships")}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' || e.key === ' ') {
                      handleSubTabClick("residential-townships");
                    }
                  }}
                >
                  Townships
                </button>
                <button
                  type="button"
                  aria-label=""
                  className={`sub-tab ${
                    activeSubTab === "residential-malls" ? "active" : ""
                  }`}
                  data-target="#residential-malls"
                  onClick={() => handleSubTabClick("residential-malls")}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' || e.key === ' ') {
                      handleSubTabClick("residential-malls");
                    }
                  }}
                >
                  Malls
                </button>
                <button
                  type="button"
                  aria-label=""
                  className={`sub-tab ${
                    activeSubTab === "residential-complex" ? "active" : ""
                  }`}
                  data-target="#residential-complex"
                  onClick={() => handleSubTabClick("residential-complex")}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' || e.key === ' ') {
                      handleSubTabClick("residential-complex");
                    }
                  }}
                >
                  Commercial Complex
                </button>
                <button
                  type="button"
                  aria-label=""
                  className={`sub-tab ${
                    activeSubTab === "residential-homes" ? "active" : ""
                  }`}
                  data-target="#residential-homes"
                  onClick={() => handleSubTabClick("residential-homes")}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' || e.key === ' ') {
                      handleSubTabClick("residential-homes");
                    }
                  }}
                >
                  Holiday Homes
                </button>
                <button
                  type="button"
                  aria-label=""
                  className={`sub-tab ${
                    activeSubTab === "residential-farm-houses" ? "active" : ""
                  }`}
                  data-target="#residential-farm-houses"
                  onClick={() => handleSubTabClick("residential-farm-houses")}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' || e.key === ' ') {
                      handleSubTabClick("residential-farm-houses");
                    }
                  }}
                >
                  Farm Houses
                </button>
              </div>
            </div>
            <div className="home_type_content">
              <div
                id="residential-apartments"
                style={{
                  display:
                    activeSubTab === "residential-apartments" ? "flex" : "none",
                }}
              >
                <img src={apartment} alt="apartment" width={780} height={455} />
                <h3 className="animate__animated animate__bounce animate__delay-05s">
                  Apartments
                </h3>
              </div>
              <div
                id="residential-plots"
                style={{
                  display:
                    activeSubTab === "residential-plots" ? "flex" : "none",
                }}
              >
                <img src={plots} alt="apartment" width={780} height={455} />
                <h3 className="animate__animated animate__bounce animate__delay-05s">
                  plots
                </h3>
              </div>
              <div
                id="residential-villas"
                style={{
                  display:
                    activeSubTab === "residential-villas" ? "flex" : "none",
                }}
              >
                <img src={villa} alt="apartment" width={780} height={455} />
                <h3 className="animate__animated animate__bounce animate__delay-05s">
                  villas
                </h3>
              </div>
              <div
                id="residential-townships"
                style={{
                  display:
                    activeSubTab === "residential-townships" ? "flex" : "none",
                }}
              >
                <img src={township} alt="apartment" width={780} height={455} />
                <h3 className="animate__animated animate__bounce animate__delay-05s">
                  townships
                </h3>
              </div>
              <div
                id="residential-malls"
                style={{
                  display:
                    activeSubTab === "residential-malls" ? "flex" : "none",
                }}
              >
                <img src={mall} alt="apartment" width={780} height={455} />
                <h3 className="animate__animated animate__bounce animate__delay-05s">
                  malls
                </h3>
              </div>
              <div
                id="residential-complex"
                style={{
                  display:
                    activeSubTab === "residential-complex" ? "flex" : "none",
                }}
              >
                <img
                  src={commercialComplex}
                  alt="apartment"
                  width={780}
                  height={455}
                />
                <h3 className="animate__animated animate__bounce animate__delay-05s">
                  commercial complex
                </h3>
              </div>
              <div
                id="residential-homes"
                style={{
                  display:
                    activeSubTab === "residential-homes" ? "flex" : "none",
                }}
              >
                <img
                  src={holidayHome}
                  alt="apartment"
                  width={780}
                  height={455}
                />
                <h3 className="animate__animated animate__bounce animate__delay-05s">
                  holiday homes
                </h3>
              </div>
              <div
                id="residential-farm-houses"
                style={{
                  display:
                    activeSubTab === "residential-farm-houses"
                      ? "flex"
                      : "none",
                }}
              >
                <img src={farmHouse} alt="apartment" width={780} height={455} />
                <h3 className="animate__animated animate__bounce animate__delay-05s">
                  farm houses
                </h3>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default HomePropertyType
