import React from "react"

const HomeProcess = ({ ProcessHead, ProcessStep }) => {
  return (
    <div className="home_process_block">
      <div className="container">
        <h2 className="animate__animated animate__slideInUp">{ProcessHead}</h2>
        <div className="home_process_area">
          {ProcessStep.length > 0 &&
            ProcessStep.map((item, index) => {
              return (
                <div
                  className={`home_process_box animate__animated animate__fadeInRight animate__delay-${index}s`}
                  key={index}
                >
                  <span>{index + 1}</span>
                  <div dangerouslySetInnerHTML={{ __html: item.process }} />
                </div>
              )
            })}
        </div>
      </div>
    </div>
  )
}

export default HomeProcess
